import * as React from 'react';
import {
  ProgressIndicator,
  ProgressIndicatorColor,
  ProgressIndicatorSize,
  ProgressIndicatorVariant,
  ProgressIndicatorType,
} from '@here/hds-progress-indicator';

// eslint-disable-next-line no-new
new ProgressIndicator();

interface HDSProgressIndicatorProperties extends React.HTMLAttributes<HTMLElement> {
  color?: ProgressIndicatorColor;
  size?: ProgressIndicatorSize;
  variant?: ProgressIndicatorVariant;
  progress?: number;
  indicatorType?: ProgressIndicatorType;
}

const HDSProgressIndicator = React.forwardRef((props: HDSProgressIndicatorProperties, ref) => {
  const {className, indicatorType, ...rest} = props;
  return (
    <hds-progress-indicator
      ref={ref}
      {...(indicatorType && {'indicator-type': indicatorType})}
      {...(className && {class: className})}
      {...rest}
    ></hds-progress-indicator>
  );
});

HDSProgressIndicator.defaultProps = {
  color: 'accent',
  variant: 'indeterminate',
  size: 'large',
  progress: 0,
  indicatorType: 'linear',
};

export default HDSProgressIndicator;
